<template>
  <div class="padding">
    <transition name="page" mode="out-in">
      <div :key="$route.params.pageIndex">
        <strong class="is-size-5">
          <span class="icon is-small">
            <i class="fa fa-home"></i>
          </span>
          {{ page.title }}
        </strong>
        <div class="dropdown is-hoverable is-pulled-right is-right is-small">
          <div class="dropdown-trigger">
            <button class="button" aria-haspopup="true" aria-controls="dropdown-menu3">
              <span>Your pages</span>
              <span class="icon is-small">
                <i class="fa fa-angle-down" aria-hidden="true"></i>
              </span>
            </button>
          </div>
          <div class="dropdown-menu" id="dropdown-menu3" role="menu">
            <div class="dropdown-content">
              <router-link v-for="(page, index) in pages" key="index" class="dropdown-item" :to="'/websites/1/build/pages/' + index">
                {{ page.title }}
              </router-link>
            </div>
          </div>
        </div>
        <hr>

        <div class="field">
          <h1 class="title">Page title</h1>
          <h2 class="subtitle">The public title of your page that appears in search results and the browser.</h2>

          <div class="control">
            <input class="input" type="text" placeholder="Page title">
          </div>
        </div>

        <div class="field">
          <h1 class="title">Page description</h1>
          <h2 class="subtitle">A short explanation of your page that appears in search results.</h2>

          <div class="control">
            <input class="input is-loading" type="text" placeholder="Page description">
          </div>
        </div>

        <div class="field">
          <h1 class="title">SEO keywords</h1>
          <h2 class="subtitle">An optional list of comma-separated words that describe your site. Most modern search engines ignore this information.</h2>

          <div class="control">
            <input class="input" type="text" placeholder="SEO keywords">
          </div>
        </div>

        <a class="button is-primary">Duplicate this page</a>
        <a class="button is-danger">Delete this page</a>
      </div>
    </transition>
  </div>
</template>

<script>
import store from '../components/Data';

export default {
  name: 'build',

  data () {
    return {
      page: data.pages[this.$route.params.pageIndex],
      pages: data.pages,
    }
  },

  created () {
    // iframe.goToPage(this.$route.params.pageIndex);
  },

  watch: {
    '$route' (to, from) {
      this.page = this.pages[to.params.pageIndex];
      // iframe.goToPage(to.params.pageIndex);
    }
  }
}
</script>

<style lang="scss" scoped>
.padding {
  padding: 10px 30px;
}

.field {
  margin-bottom: 25px;

  .title {
    font-size: 18px;
    font-weight: bold;
  }

  .subtitle {
    font-size: 14px;
    padding-top: 9px;
    margin-bottom: 12px;
  }
}

.button {
  width: 100%;
  // font-size: 18px;
  // height: 45;
  margin-bottom: 10px;
}
</style>



